.banner{
    display: flex;
    background-color: #f7f7f7;
    margin:0;
    padding:0;
}

.banner_left{
    flex: 1;
    text-align: center;
    align-self: center;
}

.banner_left h1{
    color: #424242;
    font-size: 36px;
    font-weight: 600;
}

.banner_left p{
    color: #707070;
    font-weight: 480;
    font-size: 16px;
}

.banner_right{
    flex: 1;
}

.banner_right img{
    height: 480px;
    padding: 20px;
}

.banner_button{
    border: none;
    margin-top: 10px;
    padding: 10px 20px;
    background-color: #ebeefc;
    color:#4784d6;
    font-weight: 600;
    transition: transform .5s;
}

.banner_button:hover{
    transform:scale(1.1)
}